<template>
	<view class="code-box">
		<view class="flex-box">
			<input
				type="number"
				:maxlength="maxlength"
				class="hide-input"
				@input="getVal"
			/>
			<block v-for="(item, index) in ranges" :key="index">
				<view :class="['item', { active: codeIndex === item, box: type === 'box' }]">
					<view class="line"></view>
					<block v-if="isPwd && codeArr.length >= item">
						<text class="dot">.</text>
					</block>
					<block v-else>	{{ codeArr[index] ? codeArr[index] : ''}}</block>
				</view>
			</block>
		</view>
	</view>
</template>

<script>
export default {
	props: {
		// 4/6
		maxlength: {
			type: Number,
			default: 6
		},
		isPwd: {
			type: Boolean,
			default: true
		},
		type: {
			type: String,
			default: "box"
		}
	},
	onLoad() {
		console.log(1234650)
	},
	watch: {
		maxlength: {
			immediate: true,
			handler: function(newV) {
				if (newV === 6) {
					this.ranges = [1, 2, 3, 4, 5, 6]
				} else {
					this.ranges = [1, 2, 3, 4]
				}
			}
		}
	},
	data() {
		return {
			codeIndex: 1,
			codeArr: [],
			ranges: [1, 2, 3, 4]
		}
	},
	methods: {
		getVal(e) {
			let { value } = e.detail
			let arr = value.split('')
			this.codeIndex = arr.length + 1
			this.codeArr = arr
			if (this.codeIndex > Number(this.maxlength)) {
				this.$emit('finish',this.codeArr.join(''))
			}
		}
	}
}
</script>

